/* メインコンテンツ */
.mainContent {
  width: var(--content-width);
  min-width: 390px;
  max-width: var(--content-max-width);/* 新しい変数を適用 */
  min-height: 100vh;  /* 最小高さをビューポートに設定 */
  background: var(--color-white);
  border-radius: var(--radius-m);
  flex: none;
  order: 1;
  flex-grow: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 0;
  /* フッターがあるのでパディングを削除 */
  position: relative;
  /* 相対位置を設定 */
}

/* ヘッダーセクション：タイトルとオープニング */
.headerSection {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  /* ヘッダー要素間の間隔 */
}

/* ROADセクション群：連続的に配置 */
.roadSections {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0;
  /* ROADセクション間は隙間なし */
  margin-top: 40px;
  /* ヘッダーセクションとの間隔 */
}
