/* レイアウト全体のスタイル */
.container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 51px var(--space-xxl);
  gap: 72px;
  isolation: isolate;
  position: relative;
  width: 100%;
  max-width: 1304px;
  margin: 0 auto;
  background: var(--color-background);
}

/* モバイル表示時の上部スペーサー */
.mobileSpacer {
  height: 80px; /* ハンバーガーメニューの高さ + 余白 */
  width: 100%;
  background: transparent;
}

/* レスポンシブデザイン */
@media (max-width: 768px) {
  .container {
    flex-direction: row; /* rowを維持 */
    justify-content: center; /* 中央寄せを追加 */
    padding: 0;
    gap: 0;
    align-items: flex-start; /* 上揃えを維持 */
  }
}